:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-text: "League Spartan", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
}

img {
  max-width: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 1.25rem 0 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 64em) {
  .container {
    max-width: 87.5rem;
  }
}

.wrapper {
  margin: 0 auto;
}
@media (min-width: 64em) {
  .wrapper {
    margin-left: 30%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

strong {
  font-weight: 700;
}

body {
  background-image: url("../img/background_mobile2_430x931-min.jpg");
  background-color: var(--nwcGreen);
  background-repeat: no-repeat;
  background-position: top;
  transition: all 300ms ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 40em) {
  body {
    background-image: url("../img/background_tablet_800x1074-min.jpg");
    background-size: cover;
    left: 50%;
    padding: 0;
    position: relative;
    transform: translateX(-50%);
    overflow: hidden;
    clear: both;
  }
}
@media (min-width: 64em) {
  body {
    background-image: url("../img/background_desktop_1920x1080-min.jpg");
    background-size: cover;
    background-position: center;
    left: 50%;
    padding: 0;
    position: relative;
    transform: translateX(-50%);
    overflow: hidden;
    clear: both;
  }
}

section {
  margin-top: 2.5rem;
}

.header {
  margin-bottom: 9.375rem;
}
@media (min-width: 40em) {
  .header {
    margin-bottom: 3.125rem;
  }
}
@media (min-width: 64em) {
  .header {
    margin-bottom: 0;
  }
}

.heading {
  color: var(--white);
  text-transform: uppercase;
  text-align: center;
  text-shadow: 0 0.3125rem 0.375rem var(--richBlack);
  margin-bottom: 0;
}
@media (min-width: 40em) {
  .heading {
    font-size: 7.5rem;
    line-height: 1;
  }
}
@media (min-width: 64em) {
  .heading {
    font-size: 5rem;
    padding-left: 2.5rem;
  }
}

@media (min-width: 40em) {
  .gathering {
    width: 70%;
    margin-left: 30%;
  }
}
@media (min-width: 64em) {
  .gathering {
    padding-left: 2.5rem;
    margin-left: 0;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0 1.25rem;
  position: fixed;
  left: 0;
  bottom: 0;
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .footer {
    margin-left: 30%;
    position: relative;
    left: 0;
    bottom: 0;
    margin-bottom: 0;
  }
}
@media (min-width: 64em) {
  .footer {
    margin-left: 0;
    padding-left: 3.75rem;
  }
}

.saveDate {
  margin-bottom: 1rem;
}

.dates {
  font-size: 1.125rem;
}
@media (min-width: 40em) {
  .dates {
    font-size: 1.4375rem;
  }
}

.hotel {
  font-size: 1rem;
  text-transform: uppercase;
}
@media (min-width: 40em) {
  .hotel {
    font-size: 1.375rem;
  }
}

.dates,
.hotel {
  line-height: 1.35;
  color: var(--white);
  text-shadow: 0 0.1875rem 0.1875rem var(--richBlack);
  margin: 0 0 0.9375rem 0;
}